* {
    padding: 0;
    margin: 0;
    border: 0;
}

*, *:before, *:after {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

:focus, :active {
    outline: none;
}

a:focus, a:active {
    outline: none;
}

nav, footer, header, aside {
    display: block;
}

html, body {
    height: 100%;
    width: 100%;
    font-size: 100%;
    line-height: 1;
    font-size: 14px;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}

input, button, textarea {
    font-family: inherit;
}

input::-ms-clear {
    display: none;
}

button {
    cursor: pointer;
}

button::-moz-focus-inner {
    padding: 0;
    border: 0;
}

a, a:visited {
    text-decoration: none;
}

ul li {
    list-style: none;
}

img {
    vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
    font-size: inherit;
    font-weight: 400;
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

*, ::after, ::before {
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Poppins', sans-serif;
    -webkit-font-smoothing: antialiased;
    color: #333;
}

h3, h4 {
    margin: 0 0 10px;
    font-weight: 600;
    line-height: 1.4;
    color: #022144;
    letter-spacing: -0.01em;
}

body {
    margin: 0;
    background-color: #fff;
    font-family: 'Poppins', sans-serif;
    font-size: 15px;
    line-height: 26px;
    color: #808899;
    font-weight: 400;
    position: relative;
    overflow-x: hidden;
}

footer {
    display: block;
    background-color: #111;
    padding: 30px 0;
}

body::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

body::-webkit-scrollbar-thumb {
    cursor: pointer;
    background: #dc3545;
}

body::selection {
    background-color: #dc3545;
    color: #fff;
}

